<template>
  <div class="stores">
    <Navbar></Navbar>
    <div class="taiwan">
      <div class="content">
        <svg width="550" height="600">
          <g class="counties"></g>
          <path class="county-borders"></path>
        </svg>
      </div>
      <div class="content">
        <div class="message">
          <h3>{{ h1 }}</h3>
          <h3>{{ h2 }}</h3>
        </div>
        <div class="slider" v-if="show">
          <splide :options="options">
            <!-- <splide-slide v-for="travelSlide in travel" :key="travelSlide.Id">
              <div class="col50">
                <div class="imgBx"><img :src="travelSlide.Picture1" /></div>
              </div>
            </splide-slide> -->
            <splide-slide>
              <div class="col50">
                <div class="imgBx"><img :src="src" /></div>
              </div>

              <p class="titleText">{{ name }}</p>
            </splide-slide>
            <splide-slide>
              <div class="col50">
                <div class="imgBx"><img :src="src1" /></div>
              </div>

              <p class="titleText">{{ name1 }}</p>
            </splide-slide>
            <splide-slide>
              <div class="col50">
                <div class="imgBx"><img :src="src2" /></div>
              </div>
              <p class="titleText">{{ name2 }}</p>
            </splide-slide>
          </splide>
        </div>
        <!-- <div class="message" v-if="show">
          <li v-for="(hospital, index) in hospital" :key="index">
            {{ hospital }}
          </li>
        </div> -->
      </div>
    </div>
  </div>
</template>

<script>
import "@splidejs/splide/dist/css/themes/splide-default.min.css";
import * as d3 from "d3";
import * as topojson from "topojson";
// import axios from "axios";
import data from "../assets/json/COUNTY_MOI_1090820.json";
import travelData from "../assets/json/travel.json";
import { Splide, SplideSlide } from "@splidejs/vue-splide";
export default {
  components: {
    Splide,
    SplideSlide,
  },
  data() {
    return {
      options: {
        type: "loop",
        rewind: true,
        width: 600,
        height: 350,
        perPage: 1,
        // gap: "1rem",
        perMove: 1,
        // padding: {
        //   right: "1rem",
        //   left: "1rem",
        // },
        // focus: "center",
        // rewind: true,
      },
      show: false,
      // taiwanHospital: [
      //   {
      //     country: "新北市",
      //     hospital: [
      //       "衛生福利部臺北醫院 ",
      //       "汐止國泰綜合醫院",
      //       "中英醫院附設居家護理所",
      //       "新泰綜合醫院附設居家護理所",
      //     ],
      //   },
      //   {
      //     country: "臺北市",
      //     hospital: ["臺大醫學院附設醫院北護分院", "永安家庭醫學科診所"],
      //   },
      //   {
      //     country: "基隆市",
      //     hospital: ["衛生福利部基隆醫院"],
      //   },
      //   {
      //     country: "桃園市",
      //     hospital: [
      //       "中順居家護理所",
      //       "衛生福利部桃園醫院",
      //       "仁仁居家護理所",
      //       "天成醫院附設居家護理所",
      //       "國軍桃園總醫院",
      //     ],
      //   },
      //   {
      //     country: "新竹縣",
      //     hospital: ["仁慈醫院"],
      //   },
      //   {
      //     country: "花蓮市",
      //     hospital: ["衛生福利部花蓮醫院"],
      //   },
      // ],
      country: "",
      h1: "點擊地圖",
      h2: "",
      // hospital: [],
      travel: [],
      src: "",
      src1: "",
      src2: "",
      name: "",
      name1: "",
      name2: "",
    };
  },
  methods: {
    getCountries() {
      // console.log(data);
      this.taiwanCountry = data;
      this.draw(this.taiwanCountry);
    },
    draw(mapData) {
      let projection = d3.geoMercator().center([122, 24.5]).scale(6000);
      let path = d3.geoPath(projection);
      let data = mapData.objects["COUNTY_MOI_1090820"];
      let country = [];
      let strContryid = "";
      let arr = [];

      // console.log(`123` + obj);

      // console.log(JSON.stringify(data))
      // console.log(JSON.stringify(data));
      let data2 = JSON.stringify(data);
      // console.log(JSON.parse(data2).geometries);
      let data3 = JSON.parse(data2).geometries;
      for (let i = 0; i < data3.length; i++) {
        // console.log(data3[i].properties.COUNTYNAME)
        country.push(data3[i].properties.COUNTYNAME);
      }
      // console.log(country);
      this.taiwanCountry = country;
      // console.log(this.taiwanCountry[0]);
      d3.select("g.counties")
        .selectAll("path")
        .data(topojson.feature(mapData, mapData.objects["COUNTY_MOI_1090820"]).features)
        .enter()
        .append("path")
        .attr("d", path)
        .attr("id", (d) => d.properties.COUNTYNAME + "," + d.properties.COUNTYENG)
        // .attr("eng", (d) => d.properties.COUNTYENG)
        // .attr("class", (d) => d.properties.COUNTYENG)
        .on("click", (d) => {
          // console.log();
          // console.log(d.path[0]);
          // console.log(d.path[0].id);
          console.log(d.path[0].eng);
          strContryid = d.path[0].id;
          arr = strContryid.split(",");
          this.h1 = arr[0];
          this.h2 = arr[1];
          this.getJson(arr[0]);
          // console.log(this.h1);
          //  console.log(this.taiwanHospital[0].country);
          // this.show = false;
          // for (let i = 0; i < this.taiwanHospital.length; i++) {
          //   if (this.h1 === this.taiwanHospital[i].country) {
          //     this.hospital = this.taiwanHospital[i].hospital;
          //     this.show = true;
          //   }
          // }
        });
    },
    // changeview(index) {
    //   // if (this.countryInfromation[index] == null || this.countryInfromation[index]== null || this.countryInfromation[index] == null) {
    //   // this.country = 'trash';
    //   // }
    //   if (this.countryInfromation[index] == null) {
    //     this.country = "goodgotit";
    //   } else {
    //     if (this.countryInfromation[index].squadName != null) {
    //       this.country = this.countryInfromation[index].squadName;
    //     } else if (this.countryInfromation[index].homeTown != null) {
    //       this.country = this.countryInfromation[index].homeTown;
    //     } else if (this.countryInfromation[index].formed != null) {
    //       this.country = this.countryInfromation[index].formed;
    //     }
    //   }
    // },
    getJson(e) {
      // let region = travelData.XML_Head.Infos.Info[1].Region;
      // console.log(region);

      // console.log(e);
      let arr = [];
      let Infos = travelData.XML_Head.Infos.Info;
      for (let i = 0; i < travelData.XML_Head.Infos.Info.length; i++) {
        if (e === travelData.XML_Head.Infos.Info[i].Region) {
          arr.push(Infos[i]);
        }
      }
      // console.log(JSON.stringify(arr[0].Name));
      // console.log(JSON.stringify(this.getRandomArrayElements(arr, 3)));

      // this.travel = arr;
      this.travel = this.getRandomArrayElements(arr, 3);
      // console.log(this.travel.length);
      // console.log(JSON.stringify(this.travel));
      console.log(JSON.stringify(this.travel[0].Name));
      console.log(JSON.stringify(this.travel[1].Picture1));
      console.log(JSON.stringify(this.travel[2].Picture1));
      // console.log(JSON.stringify(this.travel[1].Picture1));
      // console.log(JSON.stringify(this.travel[2].Picture1));
      this.src = this.travel[0].Picture1;
      this.src1 = this.travel[1].Picture1;
      this.src2 = this.travel[2].Picture1;
      this.name = this.travel[0].Name;
      this.name1 = this.travel[1].Name;
      this.name2 = this.travel[2].Name;
      this.show = true;
    },
    getRandomArrayElements(arr, count) {
      let shuffled = arr.slice(0),
        i = arr.length,
        min = i - count,
        temp,
        index;
      while (i-- > min) {
        index = Math.floor((i + 1) * Math.random());
        temp = shuffled[index];
        shuffled[index] = shuffled[i];
        shuffled[i] = temp;
      }
      return shuffled.slice(min);
    },
  },
  mounted() {
    this.getCountries();
    // this.getJson();
  },
};
</script>
